body{
    margin: 0;
    padding: 0;
}

body.active{
    height: 100%;
    overflow: hidden !important;
}

// To remove focus outline from inputs
*:focus{
    outline: none;
}

a{
    text-decoration: none;
}

main{
    width: 80%;
    margin-left: 10%;
}

button{
    border: none;
}