nav {
  width: 100%;
  height: 50px;
  display: flex;
  background-color: #CED4FA;
  justify-content: center;
}
nav .nav-content {
  width: 80%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
nav .nav-content .menu-toggle {
  display: none;
  width: 30px;
  height: 25px;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
  cursor: pointer;
  z-index: 999999999999999;
}
nav .nav-content .menu-toggle div {
  position: relative;
  width: 30px;
  height: 5px;
  background-color: #000;
  border-radius: 5px;
  transition: 0.1s ease-in-out;
}
nav .nav-content .menu-toggle.active .upper {
  transform: rotate(45deg);
  position: absolute;
  margin-top: 10px;
}
nav .nav-content .menu-toggle.active .middle {
  opacity: 0;
}
nav .nav-content .menu-toggle.active .lower {
  transform: rotate(135deg);
  position: absolute;
  margin-top: 10px;
}
nav .nav-content section {
  width: 60%;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
nav .nav-content section input {
  width: 240px;
  height: 30px;
  border: none;
}
nav .nav-content section input, nav .nav-content section input::placeholder {
  font-size: 20px;
}
nav .nav-content section .search-button,
nav .nav-content section .nav-toggle {
  background-color: #000;
  color: #fff;
  height: 30px;
  font-size: 20px;
  cursor: pointer;
}
nav .nav-content section .nav-toggle {
  display: flex;
  align-items: center;
  flex-direction: row;
  padding: 0px 11px;
}
nav .nav-content a {
  text-decoration: none;
  font-size: 30px;
  color: #000;
}

@media (max-width: 1441px) {
  nav .nav-content section input {
    width: 200px;
  }
  nav .nav-content section input, nav .nav-content section input::placeholder, nav .nav-content section button {
    font-size: 18px;
  }
  nav .nav-content section .nav-toggle {
    padding: 0 8px;
    font-size: 18px;
  }
  nav .nav-content section .nav-toggle img {
