    width: 20px;
    height: 20px;
  }
}
@media (max-width: 901px) {
  nav .nav-content section .search-button, nav .nav-content section input, nav .nav-content section input::placeholder {
    font-size: 14px;
  }
  nav .nav-content section .nav-toggle {
    font-size: 14px;
    padding: 0 4px;
  }
  nav .nav-content section .nav-toggle img {
    width: 17px;
    height: 17px;
  }
  nav .nav-content a {
