    font-size: 12px;
  }
  nav .nav-content section .nav-toggle img {
    width: 16px;
    height: 16px;
  }
  nav .nav-content a {
    font-size: 22px;
  }
}
@media (max-width: 631px) {
  nav .nav-content .menu-toggle {
    display: flex;
  }
  nav .nav-content section {
    display: block;
    width: 78%;
    height: 100vh;
    position: absolute;
    padding: 10px 11% 0 11%;
    top: 50px;
    left: -100%;
    background-color: #CED4FA;
    z-index: 1000;
  }
  nav .nav-content section .search-button, nav .nav-content section .nav-toggle {
    margin: 10px 0 0 0;
  }
  nav .nav-content section input {
    width: 240px;
    height: 30px;
    border: none;
  }
  nav .nav-content section input, nav .nav-content section input::placeholder {
    font-size: 20px;
  }
  nav .nav-content section .search-button,
  nav .nav-content section .nav-toggle {
    background-color: #000;
    color: #fff;
    height: 30px;
    font-size: 20px;
    cursor: pointer;
  }
  nav .nav-content section .nav-toggle {
    width: fit-content;
    display: flex;
    align-items: center;
    flex-direction: row;
    padding: 0px 11px;
  }
  nav .nav-content section.active {
    left: 0;
  }
}
